import { lang } from './Lang';
import { ui } from './UI';
class InitPageBase {
    constructor(crawler) {
        this.setWantPageWrap = ui.form.querySelector('.formOption1');
        this.setWantPage = this.setWantPageWrap.querySelector('.setWantPage');
        this.setWantPageTip1 = this.setWantPageWrap.querySelector('.setWantPageTip1');
        this.setWantPageTip2 = this.setWantPageWrap.querySelector('.setWantPageTip2');
    }
    // 初始化
    init() {
        this.appendCenterBtns();
        this.setFormOptin();
        this.appendElseEl();
    }
    destroy() {
        this.destroySelf();
        ui.clearCenterButton();
        this.crawler.destroy();
    }
    // 添加中间按钮
    appendCenterBtns() { }
    // 添加其他元素（如果有）
    appendElseEl() { }
    // 显示或隐藏指定的选项
    toggleOption(no, display) {
        for (const num of no) {
            const el = ui.form.querySelector('.formOption' + num.toString());
            el.style.display = display;
        }
    }
    // 隐藏一些选项。参数是数组，传递设置项的编号。
    hideNotNeedOption(no) {
        this.toggleOption(no, 'none');
    }
    // 显示一些选项。因为页面无刷新加载，所以一些选项被隐藏后，可能需要再次显示
    showOption(no) {
        this.toggleOption(no, 'block');
    }
    // 设置表单里的选项。主要是设置页数，隐藏不需要的选项。
    setFormOptin() {
        // 设置页数
        this.setWantPageTip1.textContent = lang.transl('_页数');
        this.setWantPageTip1.dataset.tip = lang.transl('_checkWantPageRule1Arg8');
        this.setWantPageTip2.textContent = lang.transl('_数字提示1');
        this.setWantPage.value = '1';
    }
}
export { InitPageBase };
